.mask {
  width: 100%;
  height: 100%;
  z-index: 100;
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  justify-content: center;
  align-items: center;
  transition: all .6s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
}

@keyframes rotate {
  to {
    transform: rotate(360deg);
