.loading {
  width: 100px;
  height: 100px;
}

.loading__ring {
  width: 100px;
  height: 100px;
  position: absolute;
}

.loading__ring:first-child {
  transform: skew(30deg, 20deg);
}

.loading__ring:last-child {
  transform: skew(-30deg, -20deg)scale(-1, 1);
}

.loading__ring:last-child svg {
  animation-delay: -.5s;
}

.loading__ring svg {
  fill: #fff;
  animation: rotate 1s linear infinite;
}

