.bestsellers-button {
  margin-top: 8px;
  display: inline-flex;
  padding: 14px 26px;
  align-items: flex-start;
  gap: 10px;
  border-radius: 40px;
  border: 2px solid var(--violet);
  color: var(--black);
  font-size: 14px;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: -0.14px;
  text-transform: uppercase;
  position: sticky;
  left: 1500px;
  background-color: var(--white);
}
.bookcard-image {
  width: 335px;
}

.bestsellers-list {
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.bestsellers-item-name {
  margin: 40px 0 18px 0;
  height: 18px;
  color: var(--main-text-color);
  font-size: 14px;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.42px;
  text-transform: uppercase;
}
.bestsellers-name {
  font-size: 32px;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
  letter-spacing: -1.28px;
  display: flex;
  column-gap: 4px;
}
.bestsellers-name-first {
  color: var(--black);
}
.bestsellers-name-second {
  color: var(--violet);
}
.bestsellers {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.bestsellers-button-pading {
  padding-left: 156px;
}
.bestsellers-button-up {
  width: 54px;
  height: 54px;
  flex-shrink: 0;
  border-radius: 54px;
  background: #4f2ee8;
  position: sticky;
  bottom: 140px;
  left: 1000px;
}
@media screen and (min-width: 768px) {
  .bestsellers-button-up {
    display: none;
    visibility: hidden;
  }
}
@media screen and (max-width: 768px) {
  .bestsellers-mobile {
    display: none;
    visibility: hidden;
  }
}
.bestsellers-list-second {
  display: flex;
  flex-direction: row;
  column-gap: 25px;
}
@media screen and (min-width: 768px) {
  .bestsellers-mobile {
  }
}
@media screen and (max-width: 1200px) {
  .bestsellers-tablet {
    display: none;
    visibility: hidden;
  }
}
@media screen and (min-width: 768px) {
  .bestsellers-list-second {
    width: 704px;
    column-gap: 25px;
    display: flex;
    flex-direction: row;
  }
  .bookcard-image {
    width: 218px;
  }
}
@media screen and (max-width: 375px) {
  .bookcard-image {
    width: 280px;
  }
}
@media screen and (min-width: 1200px) {
  .bookcard-image {
    width: 180px;
  }
}
@media screen and (min-width: 1200px) {
  .bestsellers-list-item {
    width: 1000px;
  }
  .bestsellers-list {
    width: 1000px;
  }
}
