/* - Перевірити на повторювані з загальними стилі
- Перенести visually hidden з основних стилів
- Шрифти */

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.section.header {
  position: sticky;
  z-index: 10;
  top: 0;
  padding: 18px 20px;
  border: 2px solid var(--elements-color);
  border-radius: 0px 0px 8px 8px;
  display: flex;
  justify-content: space-between;
  background-color: var(--bg-color-one);
}

.header-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-icon {
  width: 24px;
  height: 24px;
}

.navigation-menu {
  display: none;
}

.user-block {
  display: flex;
  gap: 14px;
  align-items: center;
}

.user-account-button {
  display: none;
}

.logo-link {
  width: 109px;
  display: flex;
  gap: 8px;
  align-items: center;
}

.switch-theme-check {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.switch-theme-check-desc {
  position: relative;
}

.switch-theme-check-desc::before {
  display: block;
  content: '';
  width: 40px;
  height: 20px;
  background-image: linear-gradient(#4f2ee8, #dcdcdc),
    linear-gradient(#ffffff, #f7f7f7);
  border-radius: 50px;
}

.switch-theme-check-desc::after {
  display: block;
  content: '';
  width: 18px;
  height: 18px;
  position: absolute;
  left: 1px;
  top: 1px;
  background-color: #ffffff;
  border-radius: 50%;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.switch-theme-check.checked + .switch-theme-check-desc::after {
  transform: translate(20px);
}

.switch-theme-check.checked + .switch-theme-check-desc::before {
  background-image: linear-gradient(#4f2ee8, #686868),
    linear-gradient(#ffffff, #f7f7f7);
}

.burger-menu-icon {
  stroke: black;
}

.close-menu-icon {
  stroke: #000;
}

.page-button {
  padding: 7px 16px;
  height: 34px;
  width: fit-content;
  display: flex;
  align-items: center;
  background-color: transparent;
  border: none;
  border-radius: 36px;
  font-size: 14px;
  /* font-family: 'DM Sans'; */
  font-weight: 700;
  line-height: 1.29;
  letter-spacing: -0.56px;
}

.page-button.cart {
  display: flex;
  gap: 4px;
}

.page-button.active {
  background-color: #eac645;
}

