    align-self: start;
    fill: #4F2EE8;
} */

.user_name {
  margin-right: 4px;
  color: #fff;
  font-size: 22px;
  /* font-family: DM Sans; */
  font-weight: 700;
  letter-spacing: -0.44px;
}

/* Dropdown */

.dropdown-menu {
  position: relative;
  top: -72px;
  height: 100vh;
  padding: 92px 20px 20px 20px;
  background-image: url(/src/images/group-min.png),
    radial-gradient(
      189.32% 190.93% at 59.76% -23.42%,
      #4f2ee8 18.03%,
      #fff 100%
    );
  background-size: 100% auto;
  background-position: bottom;
  background-repeat: no-repeat;
  overflow: hidden;
}

.sign-up-button {
  margin-top: 12px;
  padding: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 184px;
  height: 48px;
  border-radius: 18px;
  border: 1.5px solid #111;
  background: #4f2ee8;
  color: #fff;
}

.dropdown-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.top-block {
  display: flex;
  flex-direction: column;
  gap: 130px;
}

.user-info {
  display: flex;
  gap: 9px;
  align-items: center;
}

.navigation-drop-menu {
  margin: auto;
  display: flex;
  flex-direction: column;

  gap: 9px;
}

.page-button.dropdown {
  color: #fff;
  fill: #fff;
}

.page-button.dropdown.active {
  position: relative;
  left: 16px;
  color: #000;
  fill: #111;
}

.logout-button {
  padding: 14px 24px;
  height: 48px;
  width: fit-content;
  display: flex;
  align-items: center;
  gap: 4px;
  border-radius: 18px;
  border: 1.5px solid #111;
  background: #fff;
  font-size: 14px;
  /* font-family: 'DM Sans'; */
  font-weight: 500;
  line-height: 1.29;
  letter-spacing: -0.56px;
}

/* Modal */

.no-scroll {
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop {
  z-index: 20;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #4f2ee8;

  /* transform: translateX(0); */
  /* transition: opacity 300ms linear, visibility 300ms linear;  */

  overflow-y: scroll;
}

.sign-up-modal {
  position: relative;
  top: 50%;
  left: 50%;
  padding: 40px 20px;
  width: 100%;
  max-width: 335px;
  height: 384px;
  border-radius: 15px;
  border-radius: 18px;
  border: 2px solid #111;
  background-color: #202024;
  border: 1px solid rgba(17, 17, 17, 0.1);

  transform: translate(-50%, -50%);
  /* transition: transform 0.5s linear; */
}

.sign-up-modal.sign-in {
  height: 314px;
}

.btn-close-open {
  position: absolute;
  top: 14px;
  right: 14px;
  stroke: #fff;
  background-color: #202024;
}

.sign-up-form {
  margin-bottom: 14px;
  /* width: 100%; */
}

.sign-up-list-form {
  margin-bottom: 24px;
  /* width: 100%; */
  list-style: none;
  padding-inline-start: 0;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.modal-input {
  width: 100%;
  height: 50px;
  padding: 16px 24px;
  border-radius: 40px;
  border: 2px solid #f6f6f6;
  background-color: transparent;
  color: #f6f6f6;
  font-size: 16px;
  /* font-family: DM Sans; */
  font-weight: 700;
  line-height: 1.29px;
  letter-spacing: -0.16px;
  text-transform: uppercase;
}

.sign-up-icon {
  position: absolute;
  /* bottom: 200px; */
  right: 44px;
  background-color: #202024;
  stroke: #fff;
}

.sign-up-icon.mail {
  bottom: 234px;
}

.sign-up-icon.lock {
  bottom: 160px;
}

.sign-up-icon.lock.sign-in {
  bottom: 164px;
}

::placeholder {
  color: #fff;
}

.sign-up-btn {
  width: 100%;
  height: 50px;
  padding: 16px 24px;
  border: none;
  border-radius: 40px;
  background: #f6f6f6;
  color: #202024;
  font-size: 16px;
  font-family: DM Sans;
  font-weight: 700;
  line-height: 1.29;
  letter-spacing: -0.16px;
  text-transform: uppercase;
}

.sign-list {
  list-style: none;
  padding-inline-start: 0;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.sign-link {
  color: rgba(246, 246, 246, 0.5);
  font-size: 14px;
  /* font-family: DM Sans; */
  font-weight: 700;
  line-height: 18px;
  letter-spacing: -0.14px;
  text-decoration: none;
}

.sign-link.active {
  color: #4f2ee8;
  text-decoration: none;
  text-decoration-line: underline;
}

/* Media */

@media screen and (min-width: 768px) {
  .section.header {
    padding: 14px 32px;
    height: 72px;
  }

  .header-navigation {
    gap: 40px;
  }

  .navigation-menu {
    display: flex;
    list-style: none;
    align-items: center;
    padding-inline-start: 0;
  }

  .menu-burger-link {
    display: none;
  }

  .user-account-button {
    display: block;
  }

  .user-block {
    gap: 24px;
  }

  .user-account-button {
    height: 45px;
    padding: 8px 12px;
    display: flex;
    align-items: center;
    background-color: #4f2ee8;
    border: none;
    border-radius: 36px;
  }

  .user-icon {
    margin-right: 9px;
  }
}

@media screen and (min-width: 1200px) {
  .section.header {
    padding: 19px 24px;
  }

  .header-navigation {
    gap: 76px;
  }

  .navigation-menu {
    gap: 8px;
  }
}
